@use 'base/mixins' as m;
@use 'base/variables' as v;

.nav-principal{
    display: flex;
    flex-direction: column;
    margin-top: 5rem;

    a {
        text-align: center;
        color: v.$blanco;
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 1rem;
        @include m.tablet { 
            margin-bottom: 0;
         }
         &:hover{
            color: v.$secundario;
         }
         &.activo{
            color: v.$secundario;
         }
    }

    @include m.tablet { 
        flex-direction: row;
        gap: 1rem;
        margin-top: 0;
     }
}